html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

* {
  margin: 0;
  padding: 0;
}

.clear {
  float: none;
  clear: both;
}

.container {
  width: 61%;
  margin: 0 auto;
  clear: both;
}

/*header */
header {
  width: 100%;
  background-image: url("/img/bg_header.png");
  background-size: 100% 100%;
}

.header__logo img {
  margin-top: 25px;
}

.header__nav {
  float: right;
  margin-top: 55px;
}

.header__nav ul li {
  display: inline;
  margin-right: 50px;
  font-size: 18px;
  font-weight: 100;
  letter-spacing: 1px;
}

.header__nav ul li:hover {
  border-bottom: 2px solid white;
  margin-top: 3px;
}

.header__nav ul li:nth-child(6) {
  margin-right: 0px;
}

.header__nav ul li a {
  color: white;
  text-decoration: none;
  font-family: "Roboto" , sans-serif;
}

/*menu */
.header__xs__menu {
  display: none;
}

.header__xs__menu__burger {
  width: 50px;
  height: 50px;
  background: #FF8C26;
  border-radius: 50%;
  position: relative;
  float: right;
  top: 25px;
  cursor: pointer;
  z-index: 200;
}

.header__xs__menu__burger__icon {
  display: block;
  height: 2px;
  width: 18px;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -9px;
  margin-top: -1px;
}

.header__xs__menu__burger__icon:after {
  content: '';
  display: block;
  height: 2px;
  width: 18px;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -9px;
  margin-top: -5px;
  transition: 0.35s;
}

.header__xs__menu__burger__icon:before {
  content: ' ';
  display: block;
  height: 2px;
  width: 18px;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -9px;
  margin-top: 4px;
  transition: 0.35s;
}

.header__xs__menu__burger__icon.open:before {
  transform: rotate(45deg);
  margin-top: -1px;
  background: #000;
}

.header__xs__menu__burger__icon.open:after {
  transform: rotate(-45deg);
  margin-top: -1px;
  background: #000;
}

.header__xs__menu__burger.open, .header__xs__menu__burger__icon.open {
  background: #fff;
}

#overlay-menu {
  position: fixed;
  top: 0;
  background: #000;
  height: 100%;
  width: 100%;
  opacity: 0.85;
  z-index: 100;
  right: -100%;
  transition: 0.35s;
}

#overlay-menu.open {
  right: 0%;
}

.header__xs__menu__nav {
  height: 60%;
  position: relative;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  font-size: 32px;
  text-align: center;
}

.header__xs__menu__nav ul {
  display: inline-block;
  margin: auto;
  height: 100%;
}

.header__xs__menu__nav ul li {
  position: relative;
  color: #fff;
  margin-bottom: 20px;
}

.header__xs__menu__nav ul li a {
  color: #fff;
  text-decoration: none;
  font-family: "Roboto" , sans-serif;
}

.header__xs__menu__nav ul li a:after {
  content: ' ';
  display: block;
  position: absolute;
  top: 100%;
  left: 0;
  height: 3px;
  width: 0%;
  background: #fff;
  transition: 0.25s;
}

.header__xs__menu__nav ul li a:hover:after {
  width: 100%;
}

/*headerBlock */
.header__info {
  width: 50%;
  float: left;
  margin-top: 235px;
}

.header__info__title {
  font-size: 35px;
  font-family: "Roboto" , sans-serif;
  color: white;
  letter-spacing: 0.5px;
}

.header__info__text {
  margin-top: 43px;
  margin-bottom: 89px;
  font-size: 20px;
  font-family: "Roboto" , sans-serif;
  color: white;
  line-height: 1.8;
}

.big__button {
  font-size: 18px;
  font-family: "HelveticaNeue" , sans-serif;
  color: white;
  text-transform: uppercase;
  display: block;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 240px;
  text-align: center;
  text-decoration: none;
  border: 2px solid white;
  border-radius: 7px;
  margin-bottom: 50px;
  letter-spacing: 0.5px;
}

.big__button:hover {
  background-color: white;
  color: #FF6633;
  -webkit-transition: 0.6s;
  -moz-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
}

.header__image {
  margin-top: 122px;
  margin-bottom: 95px;
  width: 300px;
  float: right;
}

.header__image img {
  width: 100%;
  height: 50%;
}

/*worksBlock */
.works {
  width: 100%;
  background-color: white;
  padding-top: 100px;
  padding-bottom: 100px;
}

.works__h1 {
  text-align: center;
  font-size: 34px;
  font-family: "Roboto" , sans-serif;
  color: #343434;
}

.works__subtitle {
  text-align: center;
  color: #ff8400;
  font-size: 18px;
  font-family: "Roboto" , sans-serif;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.works__info {
  width: 31%;
  float: left;
  margin-right: 3.5%;
  margin-top: 80px;
  text-align: center;
}

.works__info:nth-child(3n+2) {
  margin-right: 0px;
}

.works__info__img {
  display: block;
  width: 40%;
  margin: 0 auto;
}

.works__info__p_title {
  color: #343434;
  font-weight: 600;
  margin-top: 50px;
  margin-bottom: 20px;
}

.works__info__p_title a {
  color: #343434;
  text-decoration: none;
}

.works__info__p_title a:hover {
  color: #5c5c5c;
}

.works__info__p {
  font-size: 18px;
  font-family: "Roboto" , sans-serif;
}

.works__info__p_text {
  color: #5c5c5c;
}

/*contactsBlock */
.contacts {
  width: 100%;
  background-image: url("/img/bg_contact.png");
  background-size: 100% 100%;
  padding-top: 145px;
  padding-bottom: 145px;
  color: white;
}

.contacts__h3 {
  text-align: center;
  font-size: 30px;
  font-family: "Roboto" , sans-serif;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.contacts__p {
  width: 750px;
  margin: 0 auto;
  margin-top: 10px;
  text-align: center;
  font-size: 18px;
  font-family: "Roboto" , sans-serif;
  color: white;
  line-height: 2;
}

.contacts__form__email {
  color: white;
  width: 590px;
  border: 2px solid white;
  border-radius: 3px;
  height: 60px;
  font-size: 17px;
  letter-spacing: 1px;
  background: none;
  outline: none;
  margin-right: 25px;
}

::-webkit-input-placeholder {
  color: white;
  padding-left: 20px;
}

::-moz-placeholder {
  color: white;
  padding-left: 20px;
}

:-moz-placeholder {
  color: white;
  padding-left: 20px;
}

:-ms-input-placeholder {
  color: white;
  padding-left: 20px;
}

/* webkit */
/* Firefox 19+ */
/* Firefox 18- */
/* IE */
.contacts__form__email:focus {
  border-color: white;
}

.contacts__form__submit {
  color: #FD8C2C;
  background: white;
  border: none;
  border-radius: 3px;
  width: 200px;
  height: 62px;
  font-size: 20px;
  cursor: pointer;
  outline: none;
  box-shadow: 0 0 20px #fc6351;
}

.contacts__form__submit:hover {
  background-color: #FD8C2C;
  color: white;
  -webkit-transition: 0.6s;
  -moz-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
}

.contacts__form {
  width: 840px;
  margin: 0 auto;
  margin-top: 90px;
}

/*footer */
footer {
  width: 100%;
  border-top: 2px solid #D8D8D8;
  background-color: white;
  padding-top: 40px;
  padding-bottom: 35px;
  margin-top: 70px;
}

footer span {
  font-size: 16px;
  font-family: "Roboto" , sans-serif;
  color: #5c5c5c;
}

.footer__icons {
  float: right;
}

.footer__icons__span {
  margin-right: 20px;
}

.footer__icons__span:nth-child(3) {
  margin-right: 0px;
}

.footer__icons__span img {
  height: 20px;
}

/*media */
@media (max-width: 1349px) {
  .nav__item {
    font-size: 13px;
  }
}

@media (max-width: 1234px) {
  .header__info {
    width: 55%;
  }
  .contacts__p {
    width: 90%;
  }
  .contacts__form {
    width: 90%;
    text-align: center;
  }
  .contacts__form__email {
    width: 57%;
  }
}

@media (max-width: 1132px) {
  .header__nav {
    display: none;
  }
  .header__xs__menu {
    display: block;
  }
  .header__logo {
    float: left;
  }
  .header__image {
    width: 250px;
    margin-top: 220px;
  }
}

@media (max-width: 985px) {
  .contacts__form__email {
    width: 100%;
    margin-right: 0px;
  }
  .contacts__form__submit {
    margin-top: 50px;
  }
}

@media (max-width: 917px) {
  .works__info {
    width: 45%;
  }
  .works__info:nth-child(3n+2) {
    margin-right: 3.5%;
  }
  .header__info {
    width: 100%;
    margin-top: 50px;
  }
  .header__image {
    display: none;
  }
}

@media (max-width: 699px) {
  .container {
    width: 100%;
  }
  .header__logo {
    margin-left: 20px;
  }
  .header__xs__menu__burger {
    margin-right: 20px;
  }
  .works__info__p {
    padding-left: 10px;
    padding-right: 10px;
  }
  .works__info {
    width: 100%;
  }
  .header__info {
    width: 90%;
    margin-left: 20px;
    text-align: center;
  }
  .big__button {
    margin: 0 auto;
    margin-bottom: 50px;
  }
  .small__text {
    display: block;
    margin: 0 auto;
    width: 220px;
  }
  .footer__icons {
    float: none;
    width: 110px;
    margin: 0 auto;
    margin-top: 10px;
  }
}
